// Floats for images in legacy blog posts

.post_content {
  // Aligning with line-height (em so as to change based on font size)
  --top-pad: 0.3333em;
  // Gap from surrounding text
  --margin: 1.5rem;

  .legacy-float-l,
  .post_body img.legacy-float-l,
  .legacy-float-r,
  .post_body img.legacy-float-r {
    margin-bottom: var(--margin);

    @include bp-s-up {
      max-width: 45%;
    }
  }

